import { Component } from '@angular/core';

import { NbMenuItem } from '@nebular/theme';
import {Permissions, AuthService} from "../auth/auth.service"
import {Http} from "@angular/http"
@Component({
  selector: 'ngx-pages',
  template: `
    <ngx-sample-layout>
      <nb-menu [items]="menu"></nb-menu>
      <router-outlet></router-outlet>
    </ngx-sample-layout>
  `,
  providers:[AuthService]
})
export class PagesComponent {
  private MENU_ITEMS: NbMenuItem[]
  private menu: NbMenuItem[]
  private role:string 
  constructor(private auth: AuthService){
      this.menu = []
      this.auth.getUser().then((res)=>{
        var data = res.json()
        this.role = data['type']
        console.log(this.role)
        this.initMenu()
        this.menu = this.MENU_ITEMS;
      }).catch((res)=>{
        console.log(res)
      })



  }
  
  
      
  isShow(targetRole: string):boolean {
    // console.log(this.role, targetRole, this.role != targetRole)
    return this.role != targetRole;
  }
  initMenu(){
    this.MENU_ITEMS = [
      {
        title: '管理员',
        group: true,
        hidden: this.isShow("管理员")
      },
      {title: '用户管理',icon:"fa fa-users", link: '/pages/admin/users', hidden: this.isShow("管理员")},
      {title: '运维管理',icon:"fa fa-wrench", link: '/pages/admin/equipment',hidden: this.isShow("管理员") },
      {title: '项目管理',icon:"fa fa-book", link: '/pages/admin/projects',  hidden: this.isShow("管理员")   },
      {
        title: '设备管理', icon:"nb-cloudy",
        hidden: this.isShow("管理员"),
        children: [
          // {
          //   title:"网关管理",
          //   link: '/pages/admin/gateway'
          // },
          {
            title:"烟感器管理",
            link: '/pages/admin/smokes',
            hidden: this.isShow("管理员")
          },
        ]
    
      },
     
      {title: '首页',icon:"nb-home", link: '/pages/common-user/index',hidden: this.isShow("普通用户")  },
      {title: '平面图',icon:"nb-grid-b-outline", link: '/pages/common-user/pic',hidden: this.isShow("普通用户") },
      {title: '地图模式',icon:"nb-location", link: '/pages/common-user/map',hidden: this.isShow("普通用户") },
      {
        title: '设备管理', icon:"nb-cloudy",
        children: [
          // {
          //   title:"网关管理",
          //   link: '/pages/common-user/equipment/gateway'
          // },
          {
            title:"烟感器管理",
            link: '/pages/common-user/equipment/smoke'
          },
        ]
        ,hidden: this.isShow("普通用户")
    
      },
      {
        title: '运维用户',
        group: true
        ,hidden: this.isShow("运维用户")
      }, 
      {title: '待处理',icon:"fa fa-tasks", link: '/pages/support/padding' ,hidden: this.isShow("运维用户")},
      {title: '已完成',icon:"fa fa-paperclip", link: '/pages/support/done',hidden: this.isShow("运维用户")},
    ];
  }
}


export function ConventULR(url){
  return url
}


